.navMenu a{
  min-width: 120px;
  display: flex;
  justify-content: space-between;
  margin: 0% 2% 0% 0%;
  color:white;
  text-decoration: none;
  font-size: 1.25em;
}
.navMenu{
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 320px;
}

@media screen and (max-width:600px) {
 .navMenu a{
   min-width: 50px;
   display: flex;
   justify-content: center;
   margin: 0% 2% 0% 0%;
   color: white;
   text-decoration: none;
   font-size: 1em;
  }
}
.link {
  text-decoration: none;
  font-size: 20px;
  color: white;
  font-family: Josefin Sans, sans-serif;
  position: relative;
  margin-left: 40px;
  scroll-behavior:smooth;
}

.activeLink{
  text-decoration: none;
  font-size: 20px;
  color: white;
  font-family: Josefin Sans, sans-serif;
  position: relative;
  margin-left: 30px;
  scroll-behavior:smooth;
}

